/* HTML */
//$( "#d5__everybody_can_see" ).load( "/js/d5__everybody_can_see/d5__everybody_can_see.html" );

$(document)
	.ready(function () {

		/*   */
		var CecutientMenuShow = 0;
		$('#CecutientBtn')
			.click(function () {
				CecutientOn();
				$.cookie('CecutientCookie', 'on', {
					expires: 365,
					path: '/',
				});
				SmallFonts(); 		//   
				WhiteStyle();		  //   
				ImageOn();   		//   
				VideoOff();			  //   
				LineHeight_100();  //    
				LetterSpacing_1(); //   
				FontSansSerif();		//    
				$.cookie('settings', 'off', {
					expires: 365,
					path: '/',
				});
				if (CecutientMenuShow === 0) {
					$('#CecutientWrapper')
						.animate({'height': '55px'}, 500);
					CecutientMenuShow = 1;
				} else {
					$('#CecutientWrapper')
						.animate({'height': '0px'}, 500);
					CecutientMenuShow = 0;
				}
				return false;
			});

		if ($.cookie('CecutientCookie') == 'on') {
			if ($.cookie('settings') == 'on') {
				$('#CecutientWrapper')
					.css('height', '280px');
				$('#m-header')
					.css('margin-top', '310px');  //      
			} else {
				$('#CecutientWrapper')
					.css('height', '55px');
				$('#m-header')
					.css('margin-top', '50px');
			}
		}
		/*    */

		/*    Cookies,   */
		//
		Cecutient_init();
		//
		/*     Cookies,   */

		/*   */
		$('#CecutientOn')
			.click(function () {
				CecutientOn();
			});
		/*  */
		$('#d5__option_settings')
			.click(function () {
				ShowSettings();
			});
		/*  */
		$('#ImageOn')
			.click(function () {
				ImageOn();
			});
		$('#ImageOff')
			.click(function () {
				ImageOff();
			});
		/*  */
		$('#VideoOn')
			.click(function () {
				VideoOn();
			});
		$('#VideoOff')
			.click(function () {
				VideoOff();
			});
		/* */
		$('#SmallFonts')
			.click(function () {
				SmallFonts();
			});
		$('#MediumFonts')
			.click(function () {
				MediumFonts();
			});
		$('#BigFonts')
			.click(function () {
				BigFonts();
			});
		/* */
		$('#WhiteStyle')
			.click(function () {
				WhiteStyle();
			});
		$('#BlackStyle')
			.click(function () {
				BlackStyle();
			});
		$('#BlueStyle')
			.click(function () {
				BlueStyle();
			});
		$('#GreenStyle')
			.click(function () {
				GreenStyle();
			});
		/* */
		$('#d5__line_height_100')
			.click(function () {
				LineHeight_100();
			});
		$('#d5__line_height_150')
			.click(function () {
				LineHeight_150();
			});
		$('#d5__line_height_200')
			.click(function () {
				LineHeight_200();
			});
		/**/
		$('#d5__letter_spacing_1')
			.click(function () {
				LetterSpacing_1();
			});
		$('#d5__letter_spacing_2')
			.click(function () {
				LetterSpacing_2();
			});
		$('#d5__letter_spacing_3')
			.click(function () {
				LetterSpacing_3();
			});
		/* */
		$('#d5__sans_serif')
			.click(function () {
				FontSansSerif();
			});
		$('#d5__serif')
			.click(function () {
				FontSerif();
			});

		/*   */
		$('#CecutientOff')
			.click(function () {
				$.cookie('CecutientCookie', 'null', {
					expires: 365,
					path: '/',
				});
				$.cookie('settings', 'null', {
					expires: 365,
					path: '/',
				});
				$.cookie('style', 'null', {
					expires: 365,
					path: '/',
				});
				$.cookie('image', 'null', {
					expires: 365,
					path: '/',
				});
				$.cookie('video', 'null', {
					expires: 365,
					path: '/',
				});
				$.cookie('fonts', 'null', {
					expires: 365,
					path: '/',
				});
				$.cookie('line_height', 'null', {
					expires: 365,
					path: '/',
				});
				$.cookie('spacing', 'null', {
					expires: 365,
					path: '/',
				});
				$.cookie('type', 'null', {
					expires: 365,
					path: '/',
				});

				window.location.reload();
				return false;
			});
	});

//   
function Cecutient_init() {
	if ($.cookie('CecutientCookie') == 'on') {
		CecutientOn();
		if ($.cookie('fonts') == 'small') {
			SmallFonts();
		}
		if ($.cookie('fonts') == 'medium') {
			MediumFonts();
		}
		if ($.cookie('fonts') == 'big') {
			BigFonts();
		}
		if ($.cookie('style') == 'white') {
			WhiteStyle();
		}
		if ($.cookie('style') == 'black') {
			BlackStyle();
		}
		if ($.cookie('style') == 'blue') {
			BlueStyle();
		}
		if ($.cookie('style') == 'green') {
			GreenStyle();
		}
		if ($.cookie('image') == 'on') {
			ImageOn();
		}
		if ($.cookie('image') == 'off') {
			ImageOff();
		}
		if ($.cookie('video') == 'on') {
			VideoOn();
		}
		if ($.cookie('video') == 'off') {
			VideoOff();
		}
		if ($.cookie('line_height') == '100') {
			LineHeight_100();
		}
		if ($.cookie('line_height') == '150') {
			LineHeight_150();
		}
		if ($.cookie('line_height') == '200') {
			LineHeight_200();
		}
		if ($.cookie('spacing') == '1') {
			LetterSpacing_1();
		}
		if ($.cookie('spacing') == '2') {
			LetterSpacing_2();
		}
		if ($.cookie('spacing') == '3') {
			LetterSpacing_3();
		}
		if ($.cookie('type') == 'sans-serif') {
			FontSansSerif();
		}
		if ($.cookie('type') == 'serif') {
			FontSerif();
		}
	}
}

/*   */
function CecutientOn() {
	$(document.body).addClass('visually');

	$('.d5__hide')
		.css('display', 'none'); // ,      
	$('#CecutientOn')
		.css('display', 'none');
	$('#CecutientOff')
		.css('display', 'inline-block');
	$('*')
		.not('#logo, .home-three-akcii-wrap, .home-two-wrap div div div div')
		.css('background', 'transparent');
	$('*')
		.not('#logo, .home-three-akcii-wrap')
		.css('color', '#000');
	$('li')
		.css('list-style-image', 'none');
	//$('li').css("list-style","none");
	$('body, html')
		.css('background', '#fff');
	$('#CecutientWrapper')
		.css('background', '#fff !important');

	$('#CecutientBtn')
		.css('display', 'none'); //      

	/*       */
	//
	$('.home-video-btn div, .about-video-btn div')
		.css('display', 'none');
	$('.callibri-module-area')
		.css('display', 'none');
	$('.d5__shop_3-2_row')
		.css('padding', '15px 0');

	$('.header')
		.css('margin-top', '60px');

	/*        */
	$.cookie('CecutientCookie', 'on', {
		expires: 365,
		path: '/',
	});
	return false;
}

/*     */

//
function ShowSettings() {
	if ($.cookie('CecutientCookie') == 'on') {
		if ($.cookie('settings') == 'on') {
			$('#CecutientWrapper')
				.animate({'height': '55px'}, 500);
			$('#m-header')
				.animate({'margin-top': '50px'}, 500);
			$.cookie('settings', 'off', {
				expires: 365,
				path: '/',
			});
		} else {
			$('#CecutientWrapper')
				.animate({'height': '280px'}, 500);
			$('#m-header')
				.animate({'margin-top': '310px'}, 500);	//      
			$.cookie('settings', 'on', {
				expires: 365,
				path: '/',
			});
		}
		return false;
	}
}

