function SmallFonts() {
	if ($.cookie('CecutientCookie') == 'on') {
		$('.main')
			.removeClass('MediumFonts BigFonts')
			.addClass('SmallFonts');
		$('p, h1, h2, h3, h4, h5 a span')
			.css('zoom', '1');
		$('.FontSize')
			.removeClass('A_FontSize');
		$('#SmallFonts')
			.addClass('A_FontSize');
		/*     */

		/*END*/
		$.cookie('fonts', 'small', {
			expires: 365,
			path: '/',
		});
		return false;
		$('.l-menu-item')
			.css('padding-bottom', '20px');
	}
}

function MediumFonts() {
	if ($.cookie('CecutientCookie') == 'on') {
		$('.main')
			.removeClass('SmallFonts BigFonts')
			.addClass('MediumFonts');
		$('p, h1, h2, h3, h4, h5 a span')
			.css('zoom', '1.3');
		$('.FontSize')
			.removeClass('A_FontSize');
		$('#MediumFonts')
			.addClass('A_FontSize');
		/*     */

		/*END*/
		$.cookie('fonts', 'medium', {
			expires: 365,
			path: '/',
		});
		return false;
	}
}

function BigFonts() {
	if ($.cookie('CecutientCookie') == 'on') {
		$('.main')
			.removeClass('SmallFonts MediumFonts')
			.addClass('BigFonts');
		$('p, h1, h2, h3, h4, h5 a span')
			.css('zoom', '1.5');
		//$('.first_nav, .second_nav').css("width","50%");
		$('.FontSize')
			.removeClass('A_FontSize');
		$('#BigFonts')
			.addClass('A_FontSize');
		/*     */

		/*END*/
		$.cookie('fonts', 'big', {
			expires: 365,
			path: '/',
		});
		return false;
	}
}

