//
function VideoOn() {
	if ($.cookie('CecutientCookie') == 'on') {
		$('#VideoOff')
			.show();
		$('#VideoOn')
			.hide();
		$('iframe')
			.show();
		$.cookie('video', 'on', {
			expires: 365,
			path: '/',
		});
		return false;
	}
}

function VideoOff() {
	if ($.cookie('CecutientCookie') == 'on') {
		$('#VideoOff')
			.hide();
		$('#VideoOn')
			.show();
		$('iframe')
			.hide();
		$.cookie('video', 'off', {
			expires: 365,
			path: '/',
		});
		return false;
	}
}

